/* Contact Section Styles */
.contact-section {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 50%, #f093fb 100%);
  position: relative;
  overflow: hidden;
}

.contact-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(15, 23, 42, 0.85);
  z-index: 1;
}

.contact-section .container {
  position: relative;
  z-index: 2;
}

.contact-header {
  text-align: center;
  margin-bottom: 4rem;
}

.contact-content {
  display: grid;
  grid-template-columns: 1fr 1.5fr;
  gap: 4rem;
  align-items: start;
}

/* Contact Info Styles */
.contact-info-card {
  background: var(--card-background);
  backdrop-filter: blur(20px);
  border: 1px solid var(--border-color);
  border-radius: 24px;
  padding: 3rem;
  height: fit-content;
  position: sticky;
  top: 2rem;
}

.contact-info-card h3 {
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 1.5rem;
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.contact-info-card p {
  color: rgba(255, 255, 255, 0.8);
  line-height: 1.6;
  margin-bottom: 2.5rem;
}

.contact-methods {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.contact-method {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1.25rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 16px;
  text-decoration: none;
  color: var(--text-light);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.contact-method::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, 
    transparent, 
    rgba(255, 255, 255, 0.1), 
    transparent
  );
  transition: left 0.6s ease;
}

.contact-method:hover::before {
  left: 100%;
}

.contact-method:hover {
  background: rgba(255, 255, 255, 0.1);
  border-color: rgba(255, 255, 255, 0.3);
  transform: translateX(10px);
}

.contact-icon {
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--gradient-primary);
  border-radius: 12px;
  flex-shrink: 0;
  color: white;
}

.contact-icon svg {
  width: 24px;
  height: 24px;
  fill: currentColor;
}

.contact-details {
  flex: 1;
}

.contact-details h4 {
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 0.25rem;
  color: var(--text-light);
}

.contact-details p {
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.7);
  margin: 0;
}

.contact-arrow {
  color: var(--accent-color);
  transition: all 0.3s ease;
  flex-shrink: 0;
}

.contact-method:hover .contact-arrow {
  transform: translateX(5px);
}

/* Form Styles */
.contact-form-container {
  background: var(--card-background);
  backdrop-filter: blur(20px);
  border: 1px solid var(--border-color);
  border-radius: 24px;
  padding: 3rem;
}

.contact-form {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.form-group label {
  font-weight: 600;
  color: var(--text-light);
  font-size: 0.95rem;
}

.form-group input,
.form-group textarea {
  padding: 1rem 1.25rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 12px;
  color: var(--text-light);
  font-size: 1rem;
  transition: all 0.3s ease;
  font-family: inherit;
  resize: vertical;
}

.form-group input:focus,
.form-group textarea:focus {
  outline: none;
  border-color: var(--primary-color);
  background: rgba(255, 255, 255, 0.08);
  box-shadow: 0 0 0 3px rgba(99, 102, 241, 0.1);
}

.form-group input::placeholder,
.form-group textarea::placeholder {
  color: rgba(255, 255, 255, 0.5);
}

.submit-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.75rem;
  margin-top: 1rem;
  min-height: 56px;
  font-size: 1.1rem;
  font-weight: 600;
  position: relative;
  overflow: hidden;
}

.submit-btn.loading {
  cursor: not-allowed;
  opacity: 0.8;
}

.loading-spinner {
  width: 20px;
  height: 20px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  border-top-color: white;
  animation: spin 1s ease-in-out infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.submit-success {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 1rem 1.5rem;
  background: rgba(34, 197, 94, 0.1);
  border: 1px solid rgba(34, 197, 94, 0.3);
  border-radius: 12px;
  color: #22c55e;
  font-weight: 500;
  margin-top: 1rem;
  animation: slideInUp 0.5s ease;
}

@keyframes slideInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* CTA Section */
.contact-cta {
  margin-top: 6rem;
  text-align: center;
  padding: 3rem;
  background: var(--card-background);
  backdrop-filter: blur(20px);
  border: 1px solid var(--border-color);
  border-radius: 24px;
  position: relative;
  overflow: hidden;
}

.contact-cta::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: var(--gradient-primary);
}

.cta-content h3 {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 1rem;
  background: var(--gradient-secondary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.cta-content p {
  font-size: 1.2rem;
  color: rgba(255, 255, 255, 0.8);
  margin-bottom: 2rem;
}

/* Social Links Section */
.social-links {
  margin-top: 4rem;
  text-align: center;
  padding: 3rem 2rem;
  background: var(--card-background);
  backdrop-filter: blur(20px);
  border: 1px solid var(--border-color);
  border-radius: 24px;
  position: relative;
  overflow: hidden;
}

.social-links::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: var(--gradient-secondary);
}

.social-links h3 {
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 2rem;
  color: var(--text-light);
}

.social-icons {
  display: flex;
  justify-content: center;
  gap: 2rem;
}

.social-icon {
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  text-decoration: none;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  border: 2px solid rgba(255, 255, 255, 0.2);
  background: rgba(255, 255, 255, 0.05);
}

.social-icon::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  transform: scale(0);
  transition: transform 0.3s ease;
  z-index: 1;
}

.social-icon svg {
  width: 28px;
  height: 28px;
  transition: all 0.3s ease;
  position: relative;
  z-index: 2;
}

.social-icon:hover {
  transform: translateY(-5px);
  border-color: transparent;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
}

.social-icon:hover::before {
  transform: scale(1);
}

.social-icon:hover svg {
  transform: scale(1.1);
  color: white;
}

/* Facebook Specific Styling */
.social-icon.facebook {
  color: #1877f2;
}

.social-icon.facebook::before {
  background: #1877f2;
}

.social-icon.facebook:hover {
  box-shadow: 0 10px 30px rgba(24, 119, 242, 0.4);
}

/* Instagram Specific Styling */
.social-icon.instagram {
  color: #e4405f;
}

.social-icon.instagram::before {
  background: linear-gradient(45deg, #f09433 0%, #e6683c 25%, #dc2743 50%, #cc2366 75%, #bc1888 100%);
}

.social-icon.instagram:hover {
  box-shadow: 0 10px 30px rgba(228, 64, 95, 0.4);
}

/* Mobile Responsiveness */
@media (max-width: 768px) {
  .contact-content {
    grid-template-columns: 1fr;
    gap: 3rem;
  }
  
  .contact-info-card,
  .contact-form-container {
    padding: 2rem 1.5rem;
    position: static;
  }
  
  .contact-method {
    padding: 1rem;
  }
  
  .contact-method:hover {
    transform: none;
  }
  
  .contact-icon {
    width: 40px;
    height: 40px;
    font-size: 1.2rem;
  }
  
  .contact-cta {
    padding: 2rem 1.5rem;
    margin-top: 4rem;
  }
  
  .cta-content h3 {
    font-size: 1.5rem;
  }
}

@media (max-width: 480px) {
  .contact-info-card,
  .contact-form-container {
    padding: 1.5rem 1rem;
  }
  
  .form-group input,
  .form-group textarea {
    padding: 0.875rem 1rem;
  }
  
  .submit-btn {
    font-size: 1rem;
    min-height: 48px;
  }
  
  .contact-methods {
    gap: 0.75rem;
  }
  
  .contact-method {
    flex-direction: column;
    text-align: center;
    gap: 0.75rem;
  }
}